@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
body {
  overflow: hidden;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.filteredResultsContainer{
    overflow: auto;
    border-radius: 10px;
    background: #F2F2F2;
    width: 330px;
    padding: 0;
    height: 348px;
    position: absolute;
    z-index: 1;
    bottom: 10px;
    right: 10px
}

.filteredResultsNav{
    width: 300px;
    display: -webkit-flex;
    display: flex;
    padding:15px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background: #F2F2F2;
    position: fixed;
    border-radius: 10px 10px 0 0;
    height: 11px;
    box-shadow: rgb(0 0 0 / 20%) 2px 1px 10px 1px;
}

.filteredResultsNodeId{
    font-family: Open Sans;
    font-weight: 500;
    font-size: 12px;
}

.nodeInfoContainer{
    overflow: auto;
    border-radius: 10px;
    background: #F2F2F2;
    width: 300px;
    padding: 15px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    max-height: 400px;
    position: absolute;
    z-index: 1;
    top: 10px;
    right: 10px
}

.nodeInfoProp, .nodeInfoTitle{
    font-family: Open Sans;
    font-size: 14px;
    margin: 5px 0;
    text-align: left;
}

.nodeInfoTitle{
    text-align: center;
    margin-bottom: 15px;
    font-size: 16px;
}

.nodeInfoDicioLink{
    font-family: Open Sans;
    color: green;
    text-decoration: underline;
    cursor: pointer;
}

.backButton{
    cursor: pointer; 
    position: absolute;
    top: 10px;
    left: 10px;
    text-transform: uppercase;
    font-family: Open Sans;
    font-weight: bold;
    font-size: 14px;
    z-index: 2; 
    width: 100px;
    height: 40px;
    background: #ff6991;
    border-radius: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
